.home-page .info-container {
    background-color: #012f34;
    padding-block: 140px;
    & .info {
        display: grid;
        grid-template-columns: 1fr 1fr;
        // padding-inline: 165px;
        padding-block-start: 26px;
        gap:125px;
        & .left {
            padding-block-start: 16px;
            & h2 {
                font-size: 48px;
                line-height: 48px;
                font-family: Livvic-Bold;
            }
        }

        & .right {
            & ul {
                & li {
                    & .item {
                        display: grid;
                        grid-template-columns: auto 1fr;
                        padding-block: 16px;
                        gap: 39px;
                        & .img-container {
                            & img {
                                height: 56px;
                                width: 56px;
                            }
                        }

                        & .content-container {
                            display: flex;
                            flex-direction: column;
                            gap: 16px;
                            & h3 {
                                color: #f67e7e;
                                font-size: 18px;
                                line-height: 28px;
                            }
                            & p {
                                font-size: 15px;
                                line-height: 25px;
                            }
                        }
                    }
                }
            }
        }
    }
}
